html{height:100%}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;background-color:#cddde2;height:100%;overflow:hidden}#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App{height:100%}main{padding:20px 0;display:flex;align-items:center;justify-content:center;margin:auto;overflow:hidden;height:100%}.sr{position:absolute;left:-10000px;top:auto;width:1px;height:1px;overflow:hidden}.error{color:#ce0202;font-weight:600}.shadow{box-shadow:10px 10px 13px -1px rgba(0,0,0,0.09)}.light-shadow{box-shadow:6px 5px 5px -1px rgba(0,0,0,0.01)}#dim{height:100%;width:100%;z-index:4;position:fixed;background-color:rgba(0,0,0,0.623);-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-duration:0.5s;transition-duration:0.5s}.errorMessage{position:absolute;z-index:3;top:80px;background-color:rgba(255,0,0,0.75);padding:5px 15px;color:white;border-radius:15px;opacity:0;-webkit-animation:fall-in 0.5s ease-in 0.25s forwards;animation:fall-in 0.5s ease-in 0.25s forwards}@-webkit-keyframes fall-in{from{-webkit-transform:translateY(-20px);transform:translateY(-20px);opacity:0}to{-webkit-transform:translateY(0px);transform:translateY(0px);opacity:1}}@keyframes fall-in{from{-webkit-transform:translateY(-20px);transform:translateY(-20px);opacity:0}to{-webkit-transform:translateY(0px);transform:translateY(0px);opacity:1}}button:hover{cursor:pointer}@media (max-width: 480px){main{padding:15px 0}}

footer{color:grey;text-align:center;font-family:'Space Mono', monospace;font-size:12px;position:absolute;bottom:0;width:100%}@media (max-width: 980px){footer{font-size:10px}}

.cornerRibbon .corner-ribbon{font-size:16px;width:260px;background:#e43;position:absolute;top:20px;left:-45px;text-align:center;line-height:50px;letter-spacing:1px;color:#000000;transform:rotate(-45deg);-webkit-transform:rotate(-45deg)}.cornerRibbon .corner-ribbon.sticky{position:fixed}.cornerRibbon .corner-ribbon.shadow{box-shadow:0 0 3px rgba(0,0,0,0.3)}.cornerRibbon .corner-ribbon.top-left{top:25px;left:-50px;transform:rotate(-45deg);-webkit-transform:rotate(-45deg)}.cornerRibbon .corner-ribbon.top-right{top:25px;right:-50px;left:auto;transform:rotate(45deg);-webkit-transform:rotate(45deg)}.cornerRibbon .corner-ribbon.bottom-left{top:auto;bottom:25px;left:-50px;transform:rotate(45deg);-webkit-transform:rotate(45deg)}.cornerRibbon .corner-ribbon.bottom-right{top:auto;right:-60px;bottom:45px;left:auto;transform:rotate(-45deg);-webkit-transform:rotate(-45deg)}.cornerRibbon .corner-ribbon.white{background:#f0f0f0;color:#555}.cornerRibbon .corner-ribbon.black{background:#333}.cornerRibbon .corner-ribbon.grey{background:#999}.cornerRibbon .corner-ribbon.blue{background:#39d}.cornerRibbon .corner-ribbon.green{background:#2c7}.cornerRibbon .corner-ribbon.turquoise{background:#1b9}.cornerRibbon .corner-ribbon.purple{background:#95b}.cornerRibbon .corner-ribbon.red{background:#e43}.cornerRibbon .corner-ribbon.orange{background:#e82}.cornerRibbon .corner-ribbon.yellow{background:#ec0}@media (max-width: 480px){.cornerRibbon{display:none}}

#backgroundImage{z-index:-1;position:fixed;opacity:0.05;background-repeat:repeat;height:100%;width:100%;top:0;left:0;background-image:url(../../static/media/checker-480x480.f0515990.jpg)}.slideIconContainer{position:absolute;top:0;left:0;height:100%;width:100%;font-size:8em;display:flex}.slideIconContainer .slideIcon{margin:auto;flex:1 1}.frame{z-index:1;display:flex;flex-direction:column;width:25rem;position:fixed;height:75%;-webkit-transform-origin:top left;transform-origin:top left;animation:rotateAni 0.5s ease-out backwards;-webkit-animation:rotateAni 0.5s ease-out backwards;overflow:none}#NoPetsFound{font-weight:600;font-size:1.5em;border-radius:5px;border:solid 0px white;background-color:white;padding:2rem 4rem;text-align:center}#NoPetsFound p{margin:5px 0}#NoPetsFound a{font-weight:400;font-size:0.7em}#NoPetsFound em{font-size:12px;font-weight:400}@-webkit-keyframes rotateAni{0%{-webkit-transform:rotate(360deg) scale(0) translateX(-10000px);transform:rotate(360deg) scale(0) translateX(-10000px);opacity:0}100%{-webkit-transform:rotate(0deg) scale(1) translateX(0px);transform:rotate(0deg) scale(1) translateX(0px);opacity:1}}@keyframes rotateAni{0%{-webkit-transform:rotate(360deg) scale(0) translateX(-10000px);transform:rotate(360deg) scale(0) translateX(-10000px);opacity:0}100%{-webkit-transform:rotate(0deg) scale(1) translateX(0px);transform:rotate(0deg) scale(1) translateX(0px);opacity:1}}.frameContents{z-index:1;min-height:75vh;background-color:white;border-radius:4px;padding:10px;display:flex;flex-direction:column}.petImage{flex:2 1;padding:10px;overflow:hidden;overflow-y:auto;cursor:pointer;margin:auto}.petImage ul{list-style:none;padding:0;margin:0;z-index:1}.petImage ul li{text-align:center}.petImage img{border-radius:3px;width:auto;max-width:100%;max-height:400px}.petImage::-webkit-scrollbar{width:12px;border-radius:5px}.petImage::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.199);border-radius:5px}.petImage::-webkit-scrollbar-thumb{border-radius:5px;background-color:#33648a9c;outline:1px solid rgba(75,81,87,0)}.profile{padding:10px}.profile h2{margin:4px 0}.profile h2 a{color:#363636;text-decoration:none}.profile h2 a:hover{color:#505050;text-decoration:underline}.profile .desc{margin-bottom:10px}.profile .desc p{display:inline;padding-right:5px;font-style:italic;color:grey}.controls{display:flex;justify-content:space-around;margin-bottom:20px}.controls button{font-size:2em;border-radius:50%;border-style:none;padding:0.5em;color:white}.controls .add{background-color:#00b66a}.controls .more{color:grey}.controls .pass{background-color:#ff2f0d;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.controls button:hover{cursor:pointer}@media (max-width: 768px){.frame{width:18rem}@-webkit-keyframes rotateAni{0%{-webkit-transform:translateX(-1000px);transform:translateX(-1000px);opacity:0}100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1}}@keyframes rotateAni{0%{-webkit-transform:translateX(-1000px);transform:translateX(-1000px);opacity:0}100%{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1}}}@media (max-width: 480px){.frame{height:65%;width:14rem}.controls button{font-size:1.5em}.profile{padding:2px 10px}.profile h2{font-size:18px}.profile .desc p{font-size:12px}}

.modal{z-index:4;position:absolute;display:flex;top:0;left:0;height:100%;width:100%;align-items:center;font-family:"Open Sans", sans-serif}.modalBody{margin:auto;background-color:white;border-radius:4px;max-height:75%;padding:10px}.modalBody #closeModal{font-size:2em;color:black;background-color:rgba(0,0,0,0);border:none;text-align:right}.modalBody #closeModal:hover{cursor:pointer}.hide-modal{display:none}#enlargedImage .modalBody{height:75%;width:60%;display:flex;flex-direction:column}#enlargedImage .modalBody a{color:#5caac3;text-decoration:none}#enlargedImage .modalBody a:hover{text-decoration:underline}#enlargedImage .modalBody a:visited{color:#5c89c3}#enlargedImage .modalBody h2,#enlargedImage .modalBody h3{color:#363636}#enlargedImage .modalBody .profile{flex:1 1;overflow:hidden;display:flex}#enlargedImage .modalBody .profile .profileImage{flex:1 1;justify-content:center;height:100%;margin:auto;align-items:center;display:flex;padding:10px}#enlargedImage .modalBody .profile .profileImage img{margin:auto;max-width:100%;max-height:100%}#enlargedImage .modalBody .profile .bio{flex:1 1;padding:10px;overflow:scroll;overflow-x:hidden;overflow-y:auto}#enlargedImage .modalBody .profile .bio h2{font-size:2em}#enlargedImage .modalBody .profile .bio p{margin:4px 0;width:100%;color:#4e4e4e}#enlargedImage .modalBody .profile .bio dl{margin-left:20px}#enlargedImage .modalBody .profile .bio dl dt{font-weight:600}#enlargedImage .modalBody .profile .bio dl dd{margin:0 10px 0 5px}#enlargedImage .modalBody .profile .bio dl span{display:block;margin:4px 0}#enlargedImage .modalBody .profile .bio dl dt,#enlargedImage .modalBody .profile .bio dl dd{display:inline;color:#575757}@media (max-width: 950px){#enlargedImage .modalBody{width:80%}#enlargedImage .modalBody .profile{flex-direction:column}#enlargedImage .modalBody .profile .bio p{margin:4px 0;width:100%}#enlargedImage .modalBody .profile .bio dl{margin-left:5px}}@media (max-width: 768px){.modalBody #closeModal{font-size:1.25em}#enlargedImage .modalBody a{font-size:21px}#enlargedImage .modalBody .profile .bio dl{font-size:12px}#enlargedImage .modalBody .profile .bio h2,#enlargedImage .modalBody .profile .bio h3{font-size:14px}}

#watchListBody{padding:10px;background-color:white;border-radius:2px;border:none;width:75%;height:80%}#watchListBody h1{margin:0 5px}#watchListBody #watchList{list-style:none;overflow-y:auto;overflow-x:hidden;display:flex;flex-wrap:wrap;width:auto;max-height:inherit;padding:20px}#watchListBody #watchList a{color:black;text-decoration:none}#watchListBody #watchList a:hover{color:#6d6d6d;text-decoration:underline}#watchListBody #watchList .watchlist-Card button{margin-top:15px;width:100%;color:red;border:none;font-weight:600;text-align:center;background-color:rgba(0,0,0,0)}#watchListBody #watchList li{padding:10px 20px;background:#dfdfdf;border-radius:5px;margin:5px;height:auto;font-size:14px;color:#535353}#watchListBody #watchList h2{margin:2px 0;font-size:16px}#watchListBody #watchList .petIcon{height:75px;width:75px;display:flex;margin:auto;border-radius:100%;background-position-x:center}#watchListBody #watchList li img{height:inherit;width:inherit;border-radius:50%;margin:auto;display:flex}

header{z-index:3;background-color:#33658a;text-align:center;position:absolute;width:100%}header #loadingbar{width:0%;height:6px;background-color:#ff5714;animation:loadAni 1s ease-in forwards;-webkit-animation:loadAni 1s ease-in forwards}@-webkit-keyframes loadAni{from{width:0%}to{width:100%}}@keyframes loadAni{from{width:0%}to{width:100%}}header .brandIcon{display:inline;color:white;margin:auto;font-size:2em}header h1{display:inline;margin:0;padding:0 15px;color:white;font-family:"Permanent Marker", cursive}#settings{background-color:rgba(0,0,0,0);border:none;position:absolute;right:3vw;height:100%;width:auto}#settings:hover{cursor:pointer}@media (max-width: 600px){#settings{right:8vw}}

#sideMenu{position:absolute;z-index:4;background-color:white;padding:5px 20px;height:100%;width:100%;max-width:25%;right:0;overflow-y:auto;overflow-x:hidden;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-duration:0.5s;transition-duration:0.5s}#sideMenu a{text-decoration:none}#sideMenu .sideHeader h2{display:inline}#sideMenu .sideHeader button{display:inline;right:0;position:absolute;border:none;background-color:rgba(0,0,0,0);font-size:25px;padding:5px 10px}#sideMenu ul{list-style:none;padding:0}#sideMenu ul li{padding:5px 0}#sideMenu .leaflet-container{height:250px;width:100%;margin:0 auto}#sideMenu #locationContainer{display:flex}#sideMenu #locationContainer input{flex:2 1;margin:0 2px}#sideMenu #locationContainer button{font-size:24px;background:none;border:none}#sideMenu input{border-radius:5px;border:none;padding:5px;background-color:#e6e6e6}#sideMenu input::-webkit-input-placeholder{font-style:italic;opacity:0.7}#sideMenu input:-ms-input-placeholder{font-style:italic;opacity:0.7}#sideMenu input::-ms-input-placeholder{font-style:italic;opacity:0.7}#sideMenu input::placeholder{font-style:italic;opacity:0.7}#sideMenu #watchList{padding:8px;font-weight:600;color:white;font-size:16px;background-color:#ff5714;border-radius:5px;border:none;text-decoration:none;display:flex}#sideMenu #map{height:200px;border-style:solid;border-color:#dadada;border-radius:5px}#sideMenu #side_filters li{display:flex;justify-content:space-evenly;position:relative}#sideMenu #side_filters li label{width:50%}#sideMenu #side_filters li select{padding:5px;width:50%}#filter_btn{padding:8px;width:100%;font-weight:600;color:white;font-size:16px;background-color:#145eff;border-radius:5px;border:none;text-decoration:none;display:flex}#reset_btn{border:none;background:none;color:#145eff;position:absolute;bottom:10px;padding:10px 0;margin:0;width:100%;text-align:center}.close{right:-30% !important;visibility:hidden}.hide{display:none}@media (max-width: 1280px){#sideMenu{max-width:45%}}@media (max-width: 768px){#sideMenu{max-width:50%}#locationContainer{display:block}}@media (max-width: 500px){#sideMenu{max-width:75%}}

